.presentation{
    display: flex;
    flex-direction: row;
    overflow: hidden;
}

.sp-wi-n{
    gap: 1rem;
}

.row{
    display: flex;
}

.sp-wi-b{
    gap: 3rem;
}

.flex{
    display: flex;
    flex-direction: row;
}

.center{
    align-items: center;
}

.title{
    margin-top: 4rem;
    h1,h2{
        text-decoration: underline;
        text-decoration-color:green;
        text-decoration-thickness:3px;
    }


    h3{
        text-decoration: underline;
        text-decoration-color:rgb(113, 252, 0);
        text-decoration-thickness:3px;
    }
    h1{
        font-size: 2rem;    }
}

.skills-show{
    display: flex;
    flex-direction: column;
}

.project-show{
    margin-top: 2rem;

    .row{
        img{
            width: 60%;
        }
}
}

.page-text-box{
    width: 80%;
    p{
        font-size:1.2rem;
    }
}

.general-display{
    margin-top: 18%;
    display: flex;
    flex-direction: column;
    gap:9.5rem;
    padding:0;
}

/*-----------------MEDIA Q-------------------------*/

@media (max-width:940px) {
    .project-show{
        .row{
            flex-direction: column;
            gap:10rem;
        }
    }

    .skills-show{
        .row{
            flex-direction: column;
        }
    }
}