.presentation-title{
    display: flex;
    gap:10rem;
    padding-bottom: 14rem;
    width: 80%;

    h1{
        width: 100%;
        font-size: 3rem;
    }
}

.image-me{
    width: 25%;
    img{
        width: 100%;
    }
}

/*-----------------------MEDIA Q---------------------------------------------------------*/

@media (max-width:940px) {
    .image-me{
        align-items: center;
        width:60%;
        display: flex;
        flex-direction: column;
    }
}

@media (max-width:940px) {
    .presentation-title{
        flex-direction: column;
        margin-left: 0;
        align-items: center;
    }
}