.element{
    width: 100%;
}

h4{
    margin: 0;
    margin-bottom: 1rem;
    display: contents;
}

.image-desc{
    margin: 0;
    width:90%;
    align-items: center;
    text-align: center;
    display: contents;
}

.image{
    display: flex;
    flex-direction: column;
    align-items: center;

    img{
        width: 99%;
        border: 1px solid rgba(128, 128, 128, 0.432);
    }
}

.look-but{
    width: 9rem;
    height: 2.5rem;
    margin-top: 0.5rem;
    background-color: rgb(0, 0, 0);
    color: rgb(175, 177, 174);
    border:2px solid rgba(142, 247, 56, 0.74);
    border-radius: 0.4rem;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.2rem;
    font-weight: bold;
    transition: all 1s;
}

.look-but:hover{
    border-color: rgb(60, 182, 12);
    background-color: black;
    color:rgba(29, 56, 29, 0.74);
    cursor: pointer;
}